import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

class Square extends React.Component {
  render() {
    let color = "";
    if (this.props.value) {
      color = this.props.value === "X" ? "blue" : "yellow";
    }
    let bold = this.props.bold;
    if (this.props.color) {
      color = "red";
      bold = "";
    }

    return (
      <button
        className={`square ${color} ${bold}`}
        onClick={() => this.props.onClick()}
      >
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  renderSquare(i, j) {
    const winner = this.props.winner;
    let isWin = false;
    if (winner && winner.length >= 3) {
      winner.forEach(val => {
        if (JSON.stringify(val) === JSON.stringify({ x: i, y: j })) {
          isWin = true;
        }
      });
    }

    const point = this.props.point;
    let bold = "";
    if (point.x === i && point.y === j) {
      bold = "green";
    }

    return (
      <Square
        color={isWin}
        bold={bold}
        value={this.props.squares[i][j]}
        onClick={() => this.props.onClick(i, j)}
      />
    );
  }
  render() {
    const squares = this.props.squares.slice();
    return (
      <div>
        {squares.map((row, i) => (
          <div className="board-row" key={i}>
            {row.map((col, j) => {
              return this.renderSquare(i, j);
            })}
          </div>
        ))}
      </div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(3)
            .fill(null)
            .map(x => new Array(3).fill(null)),
          point: {
            x: null,
            y: null
          }
        }
      ],
      stepNumber: 1,
      turn: true,
      isAsc: true
    };
  }

  handleClick(i, j) {
    const history = this.state.history;
    const squares = history[this.state.stepNumber - 1].squares.map(val =>
      val.slice()
    );
    const character = this.state.turn ? "X" : "O";
    if (squares[i][j] || calcToWin(squares, i, j, character)) {
      // return;
    }
    squares[i][j] = character;
    this.setState({
      history: history.concat([
        {
          squares: squares,
          point: { x: i, y: j }
        }
      ]),
      turn: !this.state.turn,
      stepNumber: history.length + 1
    });
  }

  jumpBack(move) {
    this.setState({
      turn: move % 2 === 0,
      stepNumber: move + 1
    });
  }

  sortHistoryAsc() {
    let history = this.state.history;
    history.shift();
    history = history.sort((a, b) => {
      let countA = 0;
      a.squares.forEach(x => {
        x.forEach(y => {
          if (y != null) {
            countA++;
          }
        });
      });

      let countB = 0;
      b.squares.forEach(x => {
        x.forEach(y => {
          if (y != null) {
            countB++;
          }
        });
      });

      return countA <= countB;
    });

    history.unshift(
      Array(3)
        .fill(null)
        .map(x => new Array(3).fill(null))
    );
    this.setState({
      history: history,
      isAsc: !this.state.isAsc
    });
  }

  sortHistoryDes() {
    let history = this.state.history;
    history.shift();
    history = history.sort((a, b) => {
      let countA = 0;
      a.squares.forEach(x => {
        x.forEach(y => {
          if (y != null) {
            countA++;
          }
        });
      });

      let countB = 0;
      b.squares.forEach(x => {
        x.forEach(y => {
          if (y != null) {
            countB++;
          }
        });
      });

      return countA >= countB;
    });
    history.unshift(
      Array(3)
        .fill(null)
        .map(x => new Array(3).fill(null))
    );
    this.setState({
      history: history,
      isAsc: !this.state.isAsc
    });
  }

  render() {
    let step = this.state.stepNumber;
    const history = this.state.history.slice(0, step);
    const current = history[step - 1];
    const squares = current.squares.slice();
    const point = current.point;

    const winnerArr = calcToWin(
      squares,
      point.x,
      point.y,
      this.state.turn ? "O" : "X"
    );

    let btnSort;
    if (this.state.isAsc) {
      btnSort = (
        <button onClick={() => this.sortHistoryAsc()}>Sort ascending</button>
      );
    } else {
      btnSort = (
        <button onClick={() => this.sortHistoryDes()}>Sort descending</button>
      );
    }

    let status;
    if (winnerArr && winnerArr.length >= 3) {
      status = "Winner: " + (this.state.turn ? "O" : "X");
    } else if (isRound(squares)) {
      status = "X and O: Tie";
    } else {
      status = "Next player: " + (this.state.turn ? "X" : "O");
    }

    return (
      <div className="game">
        <div className="game-board">
          <Board
            squares={squares}
            winner={winnerArr}
            point={point}
            onClick={(i, j) => this.handleClick(i, j)}
          />
        </div>
        <div className="game-info">
          <div>{status}</div>
          {btnSort}
          <ol>
            {this.state.history.map((val, move) => {
              const desc = move
                ? `Go to move # ${move} ( ${val.point.x}, ${val.point.y})`
                : "Go to game start";
              return (
                <li key={move}>
                  <button onClick={() => this.jumpBack(move)}>{desc}</button>
                </li>
              );
            })}
          </ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

function isRound(squares) {
  return squares.every(x => x.every(y => y != null));
}

function calcToWin(squares, i, j, character) {
  // let arr = [-1, 1, -size, size, -(size + 1), size + 1, -(size - 1), size - 1];
  let dx = [0, 0, -1, 1, -1, 1, -1, 1];
  let dy = [-1, 1, 0, 0, -1, 1, 1, -1];
  for (let k = 0, count = 1, mul = 1; k < 8; k += 2) {
    let winnerArr = [];
    winnerArr.push({ x: i, y: j });
    while (
      squares[i + mul * dx[k]] &&
      squares[i + mul * dx[k]][j + mul * dy[k]] &&
      squares[i + mul * dx[k]][j + mul * dy[k]] === character
    ) {
      winnerArr.push({ x: i + mul * dx[k], y: j + mul * dy[k] });
      count++;
      mul++;
    }
    mul = 1;
    while (
      squares[i + mul * dx[k + 1]] &&
      squares[i + mul * dx[k + 1]][j + mul * dy[k + 1]] &&
      squares[i + mul * dx[k + 1]][j + mul * dy[k + 1]] === character
    ) {
      winnerArr.push({ x: i + mul * dx[k + 1], y: j + mul * dy[k + 1] });
      count++;
      mul++;
    }
    if (count >= 3) return winnerArr;
  }
  return null;
}



